import ruiDatePicker from '@/components/rattenking-dtpicker/rattenking-dtpicker.vue';
var localdata = JSON.parse(localStorage.getItem('params'))
var createOrderData2 = JSON.parse(localStorage.getItem('createOrderData2'))
console.log(createOrderData2)
export default {
	components: {
		ruiDatePicker
	},
	data() {
		return {
			createOrderData:{},
			days:'',
			value1: '',
			value2: '',
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			name: '', //姓名
			tel: '', //电话
			payType: 1, //1微信 2支付宝
			couponList: [{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressData: {
				name: '许小星',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '352221199501010521',
				area: '149号',
				default: false,
			}
		}
	},
	onLoad(option) {
		this.createOrderData =createOrderData2
		uni.setNavigationBarTitle({
			title: createOrderData2.hotelName
		})
		let id = option.id;
		// console.log(id)
		var date = new Date();
		var seperator1 = "-";
		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		
		this.value1 =localdata.startTime;
		this.value2 =localdata.endTime;
		this.days =JSON.parse(localStorage.getItem('time'));
		
		//商品数据
		//let data = JSON.parse(option.data);
		//console.log(data);
		
		// uni.getStorage({
		// 	key:'createOrderData',
		// 	success(e){
		// 		// e.data//这就是你想要取的token
		// 		this.createOrderData =e.data
		// 		console.log(this.createOrderData)
		// 	}
		// })
		// console.log(this.createOrderData)
	},
	methods: {
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			var newData ={
				otherContactName:this.name,
				contactPhone:this.tel,
				desc:this.desc
			}
			// console.log(newData)
			uni.getStorage({
				key:'createOrderData',
				success(e){
					// e.data//这就是你想要取的token
					this.createOrderData =e.data
					var objData =Object.assign(newData, this.createOrderData)
					console.log(objData)
					 uni.setStorage({  
						key:'objData',  
						data:objData 
					});   
					let id = objData;
					id =JSON.stringify(id)
					uni.navigateTo({
						url: `/pages/money/pay?id=${id}`
					})
					
				}
			})
			console.log(this.createOrderData)
			
		},
		stopPrevent() {},
		bindChange(e) {
			this.value1=e;
		},
		bindChange1(e) {
			this.value2=e;
			var startTime=this.value1;
			var endTime=this.value2;
			// localStorage.setItem('startTime', JSON.stringify(startTime));
			// localStorage.setItem('endTime', JSON.stringify(endTime));
			localdata.startTime =startTime
			localdata.endTime =endTime
			localStorage.setItem('params', JSON.stringify(localdata));
			var curtime ='';
			startTime = new Date(startTime.replace(/-/g, "/"));
			endTime = new Date(endTime.replace(/-/g, "/"));
			curtime =endTime - startTime;
			var time = parseInt(curtime / (1000 * 60 * 60 * 24));
			localStorage.setItem('time', JSON.stringify(time));
			this.days =time
			if(time<1){
				this.$api.msg('请重新选择日期~');
			}	
			else{
				this.timeShow =true;
				this.timeDifference = time;
				// console.log(this.timeDifference+'天');
			}
			
		},
		bindCancel() {
			
		}
	}
}
